
const roomsLoaded = (newRooms) => {
  return {
    type: 'ROOMS_LOADED',
    payload: newRooms
  };
};

const roomsRequested = () => {
  return {
    type: 'ROOMS_REQUESTED'
  }
};

const roomsError = (error) => {
  return {
    type: 'ROOMS_ERROR',
    payload: error
  };
};

const roomBook = (roomId,days) => {
  return {
    type: 'ROOM_BOOKED',
    payload: roomId,
    days: days
  };
};

export {
  roomsLoaded,
  roomsRequested,
  roomsError,
  roomBook
};
