import HotelstoreService from '../services/hotelstore-service';

//console.log(HotelstoreService.data);

const initialState = {
  rooms: HotelstoreService.data,
  loading: false,
  error: null
};

const reducer = (state = initialState, action) => {

  switch (action.type) {
    case 'ROOMS_REQUESTED':
      return {
        rooms: [],
        loading: true,
        error: null
      };

    case 'ROOMS_LOADED':
      return {
        rooms: action.payload,
        loading: false,
        error: null
      };

    case 'ROOMS_ERROR':
      return {
        rooms: [],
        loading: false,
        error: action.payload
      };

    case 'ROOM_BOOKED':
      const roomId = action.payload;
      const days = action.days;

      const ID = parseInt(roomId.itemId);

      const newRooms = state.rooms.map(function(item){
        if(item.id === ID){
          return {
            ...item,
            BooksDays:days
          }
        }else{
          return item
        }
      });

      return {
        loading: false,
        error: null,
        rooms: newRooms
      };

    default:
      return state;
  }
};

export default reducer;
